.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f3f4f6; /* light gray */
  padding: 12px 24px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
}

.navbar-title {
  font-size: 20px;
  font-weight: bold;
  color: #333;
}

.navbar-links {
  display: flex;
  gap: 16px;
}

.nav-link {
  color: #2563eb; /* blue */
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease;
}

.nav-link:hover {
  text-decoration: underline;
  color: #1d4ed8;
}
